import React from 'react';
import { getRainfallCode, getRoundedAmount } from '../../../helpers/misc';

const Rainfall = ({ amount }) => {
  const rainfallCode = getRainfallCode(amount);
  const renderMeeeee = (code) => {
    if (code === 0) {
      return (
        <span>
          {'-'}
        </span>
      );
    } else {      
      let croco = null;
      let amounty = getRoundedAmount(code);

      if (code === 2 || code === 5) {
        const crocyy = code === 2 ? '<' : '>';

        croco = (
          <span className="rainfall-text--arrow smaller">
            { crocyy }
          </span>
        )
      }
      
      return (
        <React.Fragment>
          { croco }
          <span className="specialRow">
            <span className="rainfall-text--amount">{amounty}</span>
            <span className="smaller">mm</span>
          </span>
        </React.Fragment>
      );
    }
  }
      
  const jsxxxx = renderMeeeee(rainfallCode);
  
  return (
    <div className="rainfally">
      { jsxxxx }
    </div>
  );
};

export default Rainfall;


